
img {
  overflow: hidden;
  object-fit: cover;
  transition: transform 800ms;
}

.main {
  display: flex;
  max-height: 800px;
  background-color: white;
  box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.25);
}
.logo {
  position: relative;
  top: -90px;
  left: -220px;
}
.form{
    padding: 20px;
}
.form-control{
    margin-bottom: 10px;
    padding-bottom: 10px;
}
.form-control label {
  font-size: 13px;
  font-weight: 600;
  color: rgba(100, 100, 100, 1);
}

.form-control small {
  margin: -18px;
  font-size: 13px;
  font-weight: 300;
  visibility: hidden;
}

.form-control input {
  margin-top: 6px;
  background-color: rgba(241, 241, 241, 1);
  display: block;
  border: none;
  border-radius: 7px;
  font-size: 14px;
  padding: 15px;
  width: 325px;
}
.form-control input::placeholder {
  color: rgba(187, 187, 187, 1);
  font-size: 14px;
  font-weight: 200;
}
.form-control i {
  position: relative;
  top: -35px;
  left: 295px;
}
.main{
  border-radius: 20px;
}
.container1{
    background-color: rgba(248, 248, 248, 1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 600px;
    border-radius: 40px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  }
  .container2{
      overflow: hidden;
}

